import uniPopup from '@/components/uni-popup/uni-popup.vue'
import md5 from '@/common/lib/md5.min.js'
import {
	formatDate
} from '@/common/utils/datetime.js'
import {
	mapGetters
} from 'vuex'
export default {
	components: {
		uniPopup
	},
	computed: {
		...mapGetters(['user'])
	},
	data() {
		return {
			appInfo: {
				version: '',
				name: ''
			},
			item: {
				oldPassword: '',
				newPassword: ''
			}
		}
	},
	onLoad() {






	},
	methods: {
		formatDate,
		okClick() {
			const params = {
				id: this.user.id,
				password: md5(this.item.oldPassword),
				newpw: md5(this.item.newPassword)
			}
			this.$minApi.editUserPwd(params).then(res => {
				if (res) {
					this.$refs.modifyPassPopup.close()
					uni.showToast({
						title: '修改成功',
						icon: 'none'
					})
					setTimeout(() => {
						this.logout()
					}, 2000)
				} else {
					uni.showToast({
						title: '原密码不正确！',
						icon: 'none'
					})
				}
			})
		},
		modifyPass() {
			this.$refs.modifyPassPopup.open()
		},
		logout() {
			this.$store.dispatch('logout')
			uni.reLaunch({
				url: '/pages/login/login'
			})
		},
		/**
		 * app整包更新检测
		 */
		appUpgrade() {

			uni.showToast({
				title: '目前只支持Android版本软件更新',
				icon: 'none'
			})









































		}
	}
}
